<template>
	<div class="vote-view">
		<div>
			<h1>{{ vote.info.title }}</h1>
			<h3>描述：{{ vote.info.desc }}</h3>
		</div>
		<ul class="options">
			<li @click="voteup(option.id)" v-for="option in vote.options" :key="option.id">
				{{ option.content }} - {{summery[option.id].length }}票
				<div class="ratio"
					style="height: 3px; background: orange;"
					:style="{width: ratioSummer[option.id] * 100 + '%'}"
				></div>
			</li>
		</ul>
	</div>
</template>



<script>
import _ from 'lodash';
import io from 'socket.io-client';
import api from '../api';
import baseURL from '../baseURL';

window.api = api;
export default {
	name: 'VoteView',
	async mounted(){
		this.init();
	},
	data() {
		return {
			vote: {
				info: {},
				options: [],
				voteups: {},
			},
		}
	},
	watch: {
		// $route(to, from){
		// 	this.init();
		// }
	},
	methods: {
		async init(){
			var id = this.$route.params.id;
			var request = await api.get('/vote/' + id);
			var data = request.data;
			this.vote = data;
			this.vote.voteups = _.uniqBy(this.vote.voteups, 'userid');
			this.socket = io(baseURL);
			this.socket.emit('select room', id)
			this.socket.on('new vote', data => {
				this.vote.voteups = this.vote.voteups.filter(it => it.userid != data.userid)
				this.vote.voteups.push(data)
			});
		},
		voteup(optionid){
			api.post('/voteup', {
				optionid,
				voteid: this.vote.info.id,
			});
		},
	},
	computed: {
		summery(){
			var obj = _.mapValues(_.keyBy(this.vote.options, 'id'), () => []);
			return {
				...obj,
				..._.groupBy(this.vote.voteups, 'optionid'),
			};
		},
		ratioSummer(){
			return _.mapValues(this.summery, (voteups) => {
				if(voteups.length == 0){
					return 0;
				}
				return voteups.length / this.vote.voteups.length;
			})
		},
	},
}
</script>

