<template>
	<div class="login">
		<h2 style="text-align: center">登录</h2>
		<el-row>
			<el-col :span="24">
				<el-input placeholder="请输入用户名" v-model="loginInfo.name">
					<template slot="prepend">用户名：</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input placeholder="请输入密码" v-model="loginInfo.password" type="password">
					<template slot="prepend">密码：</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input placeholder="请输入验证码" v-model="loginInfo.captcha">
					<template slot="prepend">验证码：</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row><img :src="captchaUrl" @click="updateCaptcha" /></el-row>
		<el-row>
			<el-col :span="24">
				<router-link to="/forgot">
					<el-link target="_blank">
						忘记密码
					</el-link>
				</router-link>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-button @click="login" type="primary">登录</el-button>
			</el-col>
		</el-row>
	</div>
</template>


<script>
import baseURL from '../baseURL'
import api from '../api'
window.api = api;
export default {
	name: 'Login',
	data(){
		return {
			captchaUrl: baseURL + "captcha?" + Date.now(),
			loginInfo: {
				name: "",
				password: "",
				captcha: "",
			}
		}
	},
	methods: {
		async login(){
			var request = await api.post('/login', this.loginInfo);
			var data = request.data;
			if(data.code == 1){
				this.$router.push('/');
			}else if(data.code == 0){
				alert('验证码错误！');
			}else if(data.code == -1){
				alert('用户名或密码错误！');
			}
		},
		updateCaptcha(){
			this.captchaUrl = baseURL + "captcha?" + Date.now();
		},
	},
};
</script>

