<template>
	<div class="register">
		<h2>请输入您的注册信息</h2>
		<el-row>
			<el-col :span="24">
				<el-input placeholder="请输入用户名" v-model="name">
					<template slot="prepend">用户名：</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input placeholder="请输入邮箱" v-model="email">
					<template slot="prepend">邮箱：</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="24">
				<el-input placeholder="请输入密码" v-model="password" type="password">
					<template slot="prepend">密码：</template>
				</el-input>
			</el-col>
		</el-row>
		<el-row style="padding: 8px 3px">
			<el-col :span="5">
				请选择头像：
			</el-col>
			<el-col :span="19">
				<input type="file" @change="getFile($event)"/>
			</el-col>
		</el-row>
		<el-row>
			<el-button @click="register($event)">注册</el-button>
		</el-row>
	</div>
</template>


<script>
import api from '../api';

window.api = api;
export default {
	name: 'Register',
	data(){
		return {
			name: "",
			email: "",
			password: "",
			avatar: "",
		};
	},
	methods:{
		getFile(event){
			this.avatar = event.target.files[0];
		},
		async register(event){
			try{
				event.preventDefault();
				let formData = new FormData();
				formData.append('name', this.name);
				formData.append('email', this.email);
				formData.append('password', this.password);
				formData.append('avatar', this.avatar);
				await api.post('/register', formData);
				this.$router.push("/");
			}catch(e){
				alert(e.response.data.msg);
			}
		},
	},
};
</script>

